import React, { ReactElement, useEffect } from 'react';
import {
  Route, Switch, useHistory, useLocation,
} from 'react-router-dom';
import Helmet from 'react-helmet';
import '../styles/App.css';
import { isEmpty } from 'lodash';
import { Container } from '@material-ui/core';
import { UserContextValue, useUser } from '../stores/UserStore';
import { routes, RouteDescriptor } from '../pages/index';
import Layout from '../layout/Layout';
import Loading from '../components/common/Loading';
import ScrollTop from '../components/ScrollTop';

const App = () => {
  const history = useHistory();
  const location = useLocation();

  const {
    currentUser,
    isFetchingCurrentUser,
    fetchCurrentUser,
  } = useUser() as UserContextValue;

  useEffect(() => {
    fetchCurrentUser();
  }, []);

  if (isFetchingCurrentUser) {
    return <Loading message="Loading Application" />;
  }

  const isAuthenticated = currentUser !== undefined;

  if (isAuthenticated && location.pathname === '/login') {
    history.push('/');
    return null;
  }

  if (!isAuthenticated && location.pathname !== '/login') {
    history.push('/login');
    return null;
  }

  const renderRoute = (route: RouteDescriptor): ReactElement => {
    const { component: ContainerComponent } = route;
    return (
      <Route
        exact={route.exact}
        path={route.path}
        key={route.path}
        component={() => (
          <Layout user={currentUser}>
            <ScrollTop>
              <ContainerComponent />
            </ScrollTop>
          </Layout>
        )}
      />
    );
  };

  const renderRoutes = (): ReactElement[] | null => {
    if (isEmpty(routes)) {
      return null;
    }

    const renderedRoutes: ReactElement[] = routes.map((route) => renderRoute(route));
    return renderedRoutes;
  };

  return (
    <>
      <Helmet>
        <title>ASU Ambitious</title>
        <meta name="description" content="ASU Ambitious" />
        {/* <link rel="icon" href="/asset/favicon.ico" />
        <link rel="shortcut icon" href="/asset/favicon.ico" /> */}
      </Helmet>
      <Switch>
        {renderRoutes()}
      </Switch>
    </>
  );
};

export default App;
